<template>
    <div>
        <nav-bar :name="title"></nav-bar>

        <v-card class="ma-1" cols="12">
        <v-container>
        <v-row>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            required
            v-model= "store.name"
            label= "*Name"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.phone_number"
            label= "Phone number"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.email"
            label= "*Email"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.address"
            label= "Address"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            type="number"
            v-model= "store.zipcode"
            label= "Zip code"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-text-field
            class="mb-n5"
            outlined
            dense
            v-model= "store.state"
            label= "State"
            ></v-text-field>
            </v-col>
            <v-col cols="12">
            <v-select
            class="mb-n5"
            :items="items"
            label="Status"
            outlined
            dense
            v-if="isStaff"
            v-model= "store.status"
            ></v-select>
            </v-col>
            <v-col>
            <v-btn
            class="mx-4 my-2"
            color="#989898"
            dark
            @click="$router.go(-1)"
            >
            GO BACK
            </v-btn>
            <v-btn
            class="mx-4 my-2"
            color="#E47911"
            dark
            :loading="loading"
            @click= "add_store_admin"
            >
            ADD STORE
            </v-btn>
            </v-col>
        </v-row>
        </v-container>
        </v-card>
    </div>
</template>

<script>
    import NavBar from '@/components/NavBar';
    import StoreService from '@/services/StoreService';
    import Swal from 'sweetalert2'

    export default {
        components: {
        NavBar
        },

        data: () => ({
            title: 'ADD STORE',
            loading: false,
            store: {
                name: "",
                email: "",
                phone_number: "",
                address: "",
                zipcode: null,
                state: "",
                status: "PA",
            },
            items: [
            { text: 'Active', value: "A" },
            { text: 'Pending activation', value: "PA" },
            { text: 'Suspended', value: "S" },
            { text: 'Denied activation', value: "DA" },
            { text: 'Vacation', value: "V" },
            { text: 'Everyday', value: "ED" },
            ]
        }),

        computed: {
            isStaff(){
                if (this.$store.state.auth.user.is_staff)
                return true;

                return false;
            },
        },

        methods:{
            add_store_admin(){
                this.loading = true
                if(this.isStaff){
                    StoreService
                    .add_store_admin(this.store)
                    .then(res => {
                        Swal.fire({
                        icon: 'success',
                        title: 'Success',
                        text: 'The store was successfully added.'
                        }).then(() => {
                            this.loading = false
                            this.$router.go(-1)
                        })
                    })
                    .catch(e => {
                        let error = e.response.data;
                        let msg = ''
                        Object.entries(error).forEach(([ key, value ]) => {
                        msg+= key + ': ' + value + ' \n ';
                        });
                        this.loading = false;
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: msg
                        });
                    })
                }

                else{
                    StoreService
                    .add_store_customer(this.store)
                    .then(res => {
                        Swal.fire({
                        icon: 'success',
                        title: 'Success',
                        text: 'The store was successfully added.'
                        }).then(() => {
                            this.loading = false
                            this.$router.go(-1)
                        })
                    })
                    .catch(e => {
                        let error = e.response.data;
                        let msg = ''
                        Object.entries(error).forEach(([ key, value ]) => {
                        msg+= key + ': ' + value + ' \n ';
                        });
                        this.loading = false;
                        Swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: msg
                        });
                    })
                }
            },

        },

    }
</script>